import React from "react"
import { Link } from 'gatsby'

export default function Header ({ location, title }) {
  const rootPath = `${__PATH_PREFIX__}/`

  if (location.pathname === rootPath) {
    return (
      <h1
        style={{
          fontFamily: `Montserrat, sans-serif`,
          margin: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
    )
  } else {
    return (
      <h3
        style={{
          fontFamily: `Montserrat, sans-serif`,
          margin: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }
}
