import Typography from "typography"
import Wordpress2016 from "typography-theme-wordpress-2016"

Wordpress2016.baseFontSize = '18px'
Wordpress2016.baseLineHeight = 1.666
Wordpress2016.headerFontFamily = [
  "-apple-system",
  "system-ui",
  "BlinkMacSystemFont",
  "Segoe UI",
  "Roboto",
  "PingFang SC",
  "Hiragino Sans GB",
  "Source Han Sans SC",
  "Noto Sans CJK SC",
  "Microsoft YaHei",
  "WenQuanYi Micro Hei",
  "WenQuanYi Zen Hei",
  "Helvetica Neue",
  "Arial",
  "sans-serif",
]
Wordpress2016.bodyFontFamily = [
  "-apple-system",
  "system-ui",
  "BlinkMacSystemFont",
  "Segoe UI",
  "Roboto",
  "PingFang SC",
  "Hiragino Sans GB",
  "Source Han Sans SC",
  "Noto Sans CJK SC",
  "Microsoft YaHei",
  "WenQuanYi Micro Hei",
  "WenQuanYi Zen Hei",
  "Helvetica Neue",
  "Arial",
  "sans-serif",
]

Wordpress2016.overrideThemeStyles = ({ rhythm }, options, styles) => ({
  "h1": {
    fontFamily: `-apple-system,'system-ui','BlinkMacSystemFont','Segoe UI','Roboto','PingFang SC','Hiragino Sans GB','Source Han Sans SC','Noto Sans CJK SC','Microsoft YaHei','WenQuanYi Micro Hei','WenQuanYi Zen Hei','Helvetica Neue','Arial',sans-serif`,
  },
})

delete Wordpress2016.googleFonts

const typography = new Typography(Wordpress2016)

// Hot reload typography in development.
if (process.env.NODE_ENV !== `production`) {
  typography.injectStyles()
}

export default typography
export const rhythm = typography.rhythm
export const scale = typography.scale
